/**
 * @file sunlands wechat-tools
 * @author lvjinpeng
 * @data 2017/04/17
 */

/**
 * @options {object}
 * @options.title         {string}   
 * @options.desc          {string}   
 * @options.link          {string}   
 * @options.imgUrl        {string}   logo
 * @options.messageImgUrl {string}   imgUrlimgUrl
 * @options.timelineSucceed   {function}   
 * @options.timelineCancel    {function}   
 * @options.appMessageSucceed {function}   
 * @options.appMessageCancel  {function}   
 */

'use strict';

import jquery from 'jquery';
// JS
function initShare(options) {
  var aS = document.getElementsByTagName('script');
  var wxSrc = 'http://res.wx.qq.com/open/js/jweixin-1.0.0.js';
  for(var i = 0;i<aS.length;i++) {
    if (aS[i].src == wxSrc) {
      weixinShare(options);
      return;
    }
  }
  var oS = document.createElement('script');
  var s1 = aS[0];
  oS.src = wxSrc;
  s1.parentNode.insertBefore(oS, s1);
  oS.onload = function() {
    weixinShare(options);
  };
}

// 
function weixinShare(options) {
  var title = options.title || document.title;
  var desc = options.desc || '';
  var link = options.link || window.location.href;
  var imgUrl = options.imgUrl || 'http://share.sunlands.com/images/app-icon.png';
  var messageImgUrl = options.messageImgUrl || imgUrl;
  var timelineSucceed = options.timelineSucceed || function() {};
  var timelineCancel = options.timelineCancel || function() {};
  var appMessageSucceed = options.appMessageSucceed || function() {};
  var appMessageCancel = options.appMessageCancel || function() {};
  var hideSharingMenu = options.hideSharingMenu || false;

  jquery.ajax({
    // nginx
    url: '/jsapiTicket',
    type: 'get',
    cache: false,
    dataType: 'json',
    contentType: 'application/x-www-form-urlencoded; charset=utf-8',
    // url
    data: {
      'url': location.href.split('#')[0]
    },
    success: (data)=> {
      wxConfig(data.appId, data.timestamp, data.noncestr, data.signature);
      if (hideSharingMenu) {
        hideMenuItems(["menuItem:share:timeline", "menuItem:copyUrl"])
      }
      shareTimeline(title, link, imgUrl, timelineSucceed, timelineCancel);
      shareAppMessage(title, desc, link, messageImgUrl, appMessageSucceed, appMessageCancel);
    }
  });
}

// 
function wxConfig(appId, timestamp, nonceStr, signature) {
  wx.config({
    debug: false,
    appId: appId,
    timestamp: timestamp,
    nonceStr: nonceStr,
    signature: signature,
    jsApiList: [
      'onMenuShareTimeline',
      'onMenuShareAppMessage',
      'hideMenuItems'
    ]
  });
}

// 
function shareAppMessage(title, desc, link, imgUrl, successCallback, cancelCallback) {
  wx.ready(function () {
    wx.onMenuShareAppMessage({
      title: title, // 
      desc: desc, // 
      link: link, // 
      imgUrl: imgUrl, // 
      success: function () {
        // 
        successCallback && successCallback();
      },
      cancel: function () {
        // 
        cancelCallback && cancelCallback();
      }
    });
  });
}

// 
function shareTimeline(title, link, imgUrl, successCallback, cancelCallback) {
  wx.ready(function () {
    wx.onMenuShareTimeline({
      title: title, // 
      link: link, // 
      imgUrl: imgUrl, // 
      success: function () {
        // 
        successCallback && successCallback();
      },
      cancel: function () {
        // 
        cancelCallback && cancelCallback();
      }
    });
  });
}

// 
function hideMenuItems(menus) {
  wx.ready(function () {
    wx.hideMenuItems({
      menuList: menus
    });
  });
}

module.exports = initShare;



